*{
    padding: 0px;
    margin: 0px;
    transition: all 200ms ease-in;
}
.webPage{
    background-image: url(images/testimonials-bg.jpg);
    background-size: cover;
    width: 100vw;
    height: 100vh;
    overflow-x: hidden;
    overflow-y: auto;
}
.secondLayer{
    background-image: url(images/header-background.jpg);
    background-size: cover;
    height: 100vh;
    width: 100vw;
}

/* navbar css start here */
nav{
    position: sticky;
}
nav ul{
    display: flex;
    position: sticky;
    align-items: center;
    justify-content: center;
    height: 50px;
}
nav ul li{
    margin-left: 30px;
    list-style: none;
}
nav ul li a{
    color: white;
    padding: 6px;
    font-weight: bolder;
    text-decoration: none;
}
nav ul li a.active{
    color: orangered;
}
/* home css start here */
.homePage{
    text-align: center;
    width: 70%;
    margin: auto;
    margin-top: 100px;
}
.homePage h1{
    font-size: 80px;
    color: white;
    font-weight: bolder;
}
.homePage p{
    color: lightgray;
    line-height: 30px;
    font-size: 20px;
}
.homePage p strong{
    color: whitesmoke;
}
.homePage ul{
    margin-top: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.homePage ul li{
    list-style: none;
    margin-left: 30px;
    font-size: 35px;
    font-weight: bolder;
}
.homePage ul li a{
    color: white;
}
.homePage ul li a:hover{
    color: cadetblue;
}
.downKey{
    margin-top: 100px;
}
.downKey a{
    color: white;
    font-size: 40px;
}
/* //about page css start// */
.about{
    background-color: rgb(39, 38, 37);
    padding: 100px;
}
.aboutContainer{
    display: flex;
    width: 80%;
    margin: auto;
    justify-content: space-between;
}
.imageCorner img{
    width: 130px;
    border-radius: 100%;
}
.textCorner {
    width: 70%;
}
.textCorner h2{
    color: white;
    line-height: 1.4cm;
    font-size: 20px;
}
.textCorner p{
    font-size: 15px;
    color: gray;
    line-height: 30px;
}
.contectDetails{
    display: flex;
    align-items: start;
}
.btn{
    margin-top: 20px;
    margin-left: 60px;
}
.contectDetails button{
    border: none;
    border-radius: 6px;
    background-color: rgb(56, 56, 56);
    padding: 12px;
    font-size: 16px;
    font-weight: bolder;
    color: white;
}

/* Education and skills page start here */
.educationPage{
    background-color: white;
    padding: 100px;
}
.edcationContainer{
    width: 80%;
    margin: auto;
    display: flex;
    justify-content: space-between;
    align-items: start;
}
.eduCorner h3{
    border-bottom: 2px solid cadetblue;
    padding: 4px;
}
.schCorner{
    width: 70%;
}
.schCorner h2{
    line-height: 1cm;
}
.schCorner .title{
    font-size: 20px;
    font-weight: 200;
    font-style: italic;
    line-height: 1.5cm;
    color: gray;
}
.schCorner .desc{
    color: gray;
    line-height: 30px;
}
.secContainer{
    margin-top: 50px;
}
.works{
    margin-top: 50px;
}
.skillsCont{
    width: 100%;
    height: 40px;
    background-color: lightgray;
    border-radius: 4px;
}
.FillCon{
    background-color: gray;
    width: 95%;
    height: 40px;
    border-radius: 4px;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
}
.css3{
    width: 90%;
}
.Bbots{
    width: 80%;
}
.js{
    width: 80%;
}
.rect{
    width: 70%;
}
.jqry{
    width: 60%;
}
/* project and skills page */
.projectPage{
    background-color: lightgray;
    padding: 100px;
}
.projectTitle{
    text-align: center;
    color: gray;
    font-size: 15px;
    font-weight: bolder;
}
.projects{
    display: grid;
    grid-template-columns: repeat(4 , 260px);
    margin-top: 50px;
}
.probox{
    width: 200px;
    height: 200px;
    background-image: url(images/portfolio/pro\ \(1\).jpg);
    background-size: cover;
    overflow-y: hidden;
    cursor: pointer;
    margin-bottom: 20px;
    margin-left: 100px;
}
.text{
    background-color: rgba(0, 0, 0, 0.541);
    color: white;
    height: 250px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transform: translateY(100%);
    transition: all 400ms ease-in-out;
}
.probox:hover .text{
    transform: translateY(0%);
}
.icon{
    margin-top: 20px;
    font-size: 30px;
    transition: all 400ms ease-in-out;
}
.icon:hover{
    color: cadetblue;
}
.pro2{
    background-image: url(images/portfolio/pro\ \(2\).jpg);
    background-size: cover;
}
.pro3{
    background-image: url(images/portfolio/pro\ \(3\).jpg);
    background-size: cover;
}
.pro4{
    background-image: url(images/portfolio/pro\ \(4\).jpg);
    background-size: cover;
}
.pro5{
    background-image: url(images/portfolio/pro\ \(5\).jpg);
    background-size: cover;
}
.pro6{
    background-image: url(images/portfolio/pro\ \(6\).jpg);
    background-size: cover;
}
.pro7{
    background-image: url(images/portfolio/pro\ \(7\).jpg);
    background-size: cover;
}
.pro8{
    background-image: url(images/portfolio/pro\ \(8\).jpg);
    background-size: cover;
}
/* //Project Ditals form */
.projectContainerAdd{
    position: absolute;
    width: 87%;
    background-color: rgba(220, 220, 220, 0.465);
    text-align: center;
    top: 0px;
    overflow: hidden;
    left: 0px;
    height: 75%;
    padding: 80px;
}
.projectForm{
    border-radius: 6px;
    border: none;
    z-index: 100;
    background-color: white;
    width: 40%;
    margin: auto;
}
.imgSec{
    height: 200px;
}
.imgSec img{
    width: 100%;
    height: 100%;
}
.bio{
    text-align: start;
    padding: 20px;
}
.headingName{
    font-size: 20px;
    line-height: 2cm;
    font-weight: bolder;
}
.bioTag{
    line-height: 30px;
    font-style: italic;
}
.projectForm button{
    background-color: white;
    color: green;
    border: none;
    padding: 12px;
    font-size: 16px;
    cursor: pointer;
}
/* moreAbout page css start here */
.moreAbout{
    background-color: rgb(39, 38, 37);
    padding: 50px;
}
.moreContainer{
    display: flex;
    justify-content: space-between;
    width: 80%;
    margin: auto;
    align-items: center;
}
.iconCorner{
    font-size: 100px;
    color: white;
}
.textCornerMore{
    width: 80%;
}
.textCornerMore h4{
    color: whitesmoke;
    font-size: 30px;
    font-weight: bolder;
    line-height: 1cm;
}
.textCornerMore p{
    width: 69%;
    color: whitesmoke;
    line-height: 30px;
    font-weight: 200;
    font-style: italic;
    font-size: 18px;
}
.newContainer{
    padding: 50px;
    height: 300px;
}
.textSlider{
    padding: 20px;
    margin-left: 50px;
    margin-top: 50px; 
}
.newWay{
    display: flex;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.204);
    height: 400px;
}
.dot p{
    color: rgba(255, 255, 255, 0.424);
    margin-top: 40px;
    font-size: 40px;
    margin-left: 200px;
}
.dot span{
    cursor: pointer;
}
.dot p .active{
    color: white;
}
/* contactPage css start form here */
.contactPage{
    background-color: rgb(39, 38, 37);
    padding: 100px;
}
.contactContainer{
    width: 80%;
    margin: auto;
    display: flex;
    justify-content: space-between;
}
.mail p{
    color: whitesmoke;
    font-size: 100px;
}
.contactDisc{
    width: 80%;
}
.contactDisc p{
    color: gray;
    line-height: 1cm;
    font-size: 20px;
    font-weight: 200;
    font-style: italic;
}
.formSection form {
    width: 60%;
}
.formSection form p {
    color: whitesmoke;
    font-size: 18px;
    margin-top: 20px;
}
.formSection form p span{
    color: cadetblue;
}
.formSection form p input {
    background-color: rgb(65, 63, 63);
    border: none;
    border-radius: 6px;
    color: whitesmoke;
    padding: 6px;
    height: 50px;
    width: 80%;
}
.formSection form p input:hover{
    background-color: cadetblue;
}
.formSection{
    display: flex;
    justify-content: space-between;
    width: 80%;
    margin: auto;
    margin-top: 50px;

}
.formSection form p textarea{
    background-color: rgb(65, 63, 63);
    border: none;
    border-radius: 6px;
    padding: 6px;
    color: whitesmoke;
    width: 80%;
}
.formSection form p textarea:hover{
    background-color: cadetblue;
}
.formSection form p button{
    background-color: black;
    padding: 18px;
    width: 50%;
    border: none;
    border-radius: 6px;
    color: whitesmoke;
    margin-top: 30px;
    cursor: pointer;
    font-size: 18px;
}
.personalInfo{
    width: 40%;
    padding: 12px;
}
.info h4{
    color: whitesmoke;
    line-height: 1cm;
}
.info p{
    color: gray;
    line-height: 30px;
}
.latest{
    margin-top: 40px;
}
.latest a{
    color: cadetblue;
}
.latest p{
    width: 85%;
}
.newdate{
    margin-top:20px;
}
.date{
    margin-top: 20px;
    color: gray;
    font-size: 14px;

}
/* footer css start here */
.footer{  
    background-color: black; 
}
.upArrow{
    background-color: black;
    height: 60px;
}
.upArrow p{
    text-align: center;
}
.upArrow p i{
    color: white;
    font-size: 60px;
    z-index: 1;
    cursor: pointer;
}
.footer ul{
    display: flex;
    justify-content: center;
    margin-top: 20px;
}
.footer ul li{
    list-style: none;
    margin-left: 50px;
}
.footer ul li a{
    color: gray;
    font-size: 40px;
}
.footer ul li a:hover{
    color: cadetblue;
}
.disgner{
    color: gray;
    text-align: center;
    margin-top: 12px;
    padding: 50px;
}
.disgner span{
    color: cadetblue;
    font-size: 20px;
}